<template>
  <footer>
    <router-link v-if="loginAsEditor" :to="{ name: 'addEvent', params: routeParams}"><i class="icon icon-plus-squared"></i></router-link>
    <router-link :to="{ name: 'login', params: routeParams}">
      <i v-if="login" class="icon icon__logout icon-logout"></i>
      <i v-if="!login" class="icon icon__login icon-login"></i>
    </router-link>
  </footer>
</template>

<style>
  @import "variables.css";

  footer {
    display: block;
    box-sizing: border-box;
    text-align: center;
    z-index: 999;
  }

  footer > div {
    display: inline-block;
  }

  @media (--timeline-large) {
    footer {
      padding: 1.05rem 0 0 2rem;
      position: fixed;
      bottom: 0;
      width: 0;
    }

  }

  @media (--header-large) {
    footer {
      top: var(--headerHeightLg);
    }
  }



  @media (--timeline-small) {
    footer {
      position: fixed;
      background: #16407d;
      width: 31rem;
      left: 50%;
      margin-left: -15.5rem;

      text-align: left;
      bottom: 0.5rem;
    }
  }

  footer .icon {
    display: inline-block;
    color: #16407d;
    background: #fff;
    z-index: 905;
    border: 1px solid #16407d;
    font-size: 1.6rem;
    padding: .2rem;
    cursor: pointer;
  }

  @media (--timeline-large) {
    footer .icon__logout,
    footer .icon__login,
    footer .icon-plus-squared{
       position: fixed;
       bottom: 0.3rem;
       left: 0.5rem;
    }

    footer .icon-plus-squared {
      left: 16.5rem
    }
  }

  @media (--timeline-small) {
    footer .icon {
      box-shadow: none;
    }

    footer .icon__logout,
    footer .icon__login {
       float: right;
    }
  }
</style>

<script>
  import { mapState } from 'vuex'
  import FooterCalendarButton from './FooterCalendarButton'

  export default {
    components: {
      FooterCalendarButton
    },
    computed: {
      ...mapState({
        routeParams (state){
          return state.route.params;
        },
        loginAsEditor(state) {
          return !!state.user && state.user.authorities.indexOf('ROLE_ADMIN') > -1;
        },
        login(state) {
          return !!state.user;
        }
      })
    }

  }
</script>



