<template>
  <header class="header">
    <img class="logo-chevron" v-bind:src="mapImg" alt="">
  </header>
</template>

<style>
  @import "variables.css";

  .header {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: stretch;
    align-items: center;
    background: var(--headerBgColor);
    height: var(--headerHeightLg);
    padding-top: 0px;
    box-sizing: border-box;
    z-index: 999999;
  }

  .logo-chevron {
    position: absolute;
    left: 23px;
    top: -12px;
    width: 32px;
    z-index: 900;
  }

  @media (--header-large) {
    .header {
      display: none;
    }
  }

  @media (--header-medium) {
    .header {
      height: var(--headerHeightMd);
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: flex-start;
      align-content: stretch;
      align-items: stretch;
      display: none;
    }

    .logo-chevron {
      top:-10px;
      width: 23px;
    }
  }

  @media (--header-small) {
    .logo-chevron {
      left: 50%;
      margin-left: -1.6rem;
      top:.5rem;
    }

  }




</style>

<script>
  import EventFilter from './EventFilter'

  export default {
    components: {
      EventFilter
    },
    computed: {
      mapImg () {
        var image = require('../assets/img/logo.svg');
        return image;
      }
    }
  }
</script>



