<template>
  <div>
    <div class="menu-toggle">
      <input type="checkbox" class="meni-icon-checkbox" id="menuBar">
      <label class="meni-icon" for="menuBar"><i class="icon-menu"></i></label>
      <section id="sidebar-wrapper">
        <div class="logo-brand">NCFC Calendar</div>
        <calendar></calendar>
        <calendar-settings></calendar-settings>
        <event-filter></event-filter>
        <footer-buttons></footer-buttons>
      </section>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  #sidebar-wrapper {
    background: #16407d;
    float: left;
    position: fixed;
    width: 20rem;
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    padding: 0 .5rem;
  }

  #sidebar-wrapper .logo-brand {
    background: url(../assets/img/logo-new.png) no-repeat 0 0;
    text-indent: -99999px;
    width: 15rem;
    height: 11.2rem;
    display: block;
    text-align: center;
    margin: 2rem auto 3rem;
  }

  .meni-icon-checkbox,
  .meni-icon {
    display: none;
  }

  .menu-toggle {
    position: absolute;
    top: 0;
  }

  @media (--timeline-small) {
    .menu-toggle {
      position: absolute;
      top: 0;
    }

    #sidebar-wrapper {
      display: none;
    }

    .menu-toggle .icon-menu {
      display: inline-block;
      width: 4.2rem;
      height: 4.2rem;
      line-height: 4rem;
      text-align: center;
      font-size: 2.4rem;
      color: #E9BE29;
      cursor: pointer;
      display: none;
    }

    .menu-toggle .icon-menu,
    .meni-icon{
      display: block;
    }

    .meni-icon-checkbox:checked + .meni-icon + #sidebar-wrapper {
      display: block;
      z-index: 900;
      top: 0;
      width: 100%;
    }

    .meni-icon-checkbox:checked + .meni-icon i:before {
      content: '\e802';
      position: relative;
      z-index: 999;
    }

    .meni-icon-checkbox:checked + .meni-icon {
      position: relative;
      z-index: 9999 !important;
    }
  }



</style>

<script>
  import EventFilter from './EventFilter'
  import Calendar from './Calendar/Calendar'
  import CalendarSettings from './Calendar/CalendarSettings'
  import FooterButtons from './Footer'

  export default {
    components: {
      EventFilter,
      Calendar,
      CalendarSettings,
      FooterButtons
    }
  }
</script>



